@import url(https://fonts.googleapis.com/css2?family=Graduate&display=swap);
/* TODO Add site wide styles */
body {
    background-color: #A51C30 !important;
    width: 100%;
    font-family: 'Graduate' !important;
}

.harvard-art {
    color: #fcb315;
    font-size: 75pt;
    text-align: center;
    border-bottom: solid black 5pt;
}

.harvard-art-p {
    color: #fcb315;
    font-size: 35pt;
    text-align: center;
    -webkit-text-decoration: underline solid black;
            text-decoration: underline solid black;
}

.navBrand, .nav-link-color {
    color: #ffde2d !important;
    font-weight: bold;
}
.active {
    font-weight: bolder;
    color: #4db848 !important;
}
.gallery-images {
    height: 100%;
    width: 100%;
    /* margin: 5px; */
    /* padding: 5px; */
    border: 20px solid #ec8f9c;
    border-radius: 30%;



}
.gallery-image-div {
    display: flex;
    align-items: center;
    justify-content: center;
}
.gallery-image-grid {
    display: grid;
    background-color: #ed1b34;
    grid-template-columns:  80px 300px 300px 300px 300px 300px 80px;
    grid-template-rows: 20px 350px 350px 20px;
    grid-gap: 15px;
    height: 780px;
    width: 1765px;
    border-radius: 3%;
    /* margin: 10px; */
}

#image-grid-container-0 {
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row: 2/2;

}

#image-grid-container-1 {
    grid-column-start: 3;
    grid-column-end: 3;
    grid-row: 2/2;

}

#image-grid-container-2 {
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row: 2/2;

}

#image-grid-container-3 {
    grid-column-start: 5;
    grid-column-end: 5;
    grid-row: 2/2;

}

#image-grid-container-4 {
    grid-column-start: 6;
    grid-column-end: 6;
    grid-row: 2/2;

}

#image-grid-container-5 {
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row: 3/3;

}

#image-grid-container-6 {
    grid-column-start: 3;
    grid-column-end: 3;
    grid-row: 3/3;

}

#image-grid-container-7 {
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row: 3/3;

}

#image-grid-container-8 {
    grid-column-start: 5;
    grid-column-end: 5;
    grid-row: 3/3;

}

#image-grid-container-9 {
    grid-column-start: 6;
    grid-column-end: 6;
    grid-row: 3/3;

}
.artwork-title {
    font-weight: bold;
    color: #fcb315
}

.artwork-images {
    display: inline;
    height: 600px;
    width: 600px;
    padding: 11px;
    
}

.image-div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.artwork-text {
    font-weight: normal;
    text-decoration: none;
}

.artwork-link {
    display: block;
    text-align: center;
    color: #4db848;
}

#return-to-gallery {
    display: flex;
    font-size: 15pt;
    justify-content: center;
    align-items: center;
    color: #4db848;
    font-weight: 700;
    -webkit-text-decoration: black overline;
            text-decoration: black overline;
    border-bottom: black dotted 1px;
    text-align: center;
    
}

.redirect-description {
    text-align: center;
    font-weight: 900;
    color: #fcb315;
}

.artwork-text {
    color: #ffde2d !important;
}
.gallery-name {
    text-align: center;
    color: #fcb315;
}

.harvard-header {
    text-align: center;
    color: #fcb315;
    border-top: black solid 5px;
    width: 100%;
}


